'use client';

import { useCallback, useEffect, useLayoutEffect, useRef, useState } from 'react';

import type { DetailedHTMLProps, HTMLAttributes, MouseEventHandler, WheelEventHandler } from 'react';

interface OffsetProps
{
	/**
	 * x 
	 */
	x: number;

	/**
	 * y 
	 */
	y: number;
}

export interface ZoomPannerControllerProps
{
	/**
	 *  
	 *
	 * @param {number} value: 
	 */
	zoom: (value: number) => void;

	/**
	 *   
	 */
	zoomIn: () => void;

	/**
	 *   
	 */
	zoomOut: () => void;

	/**
	 *  
	 */
	reset: () => void;
}

export type ZoomPannerControllerHandler = (func: ZoomPannerControllerProps) => void;
export type ZoomPannerZoomChangeHandler = (zoom: number) => void;

export interface ZoomPannerProps extends DetailedHTMLProps<HTMLAttributes<HTMLDivElement>, HTMLDivElement>
{
	/**
	 *  
	 */
	readonly defaultZoom?: number;

	/**
	 *  
	 */
	readonly zoomUnit?: number;

	/**
	 *   
	 */
	readonly controller?: ZoomPannerControllerHandler;

	/**
	 *    
	 */
	readonly onZoomChange?: ZoomPannerZoomChangeHandler;
}

/**
 *  organism   
 *
 * @param {ZoomPannerProps} param0: ZoomPannerProps
 *
 * @returns {React.JSX.Element} JSX
 */
export default function ZoomPanner({ defaultZoom = 1, zoomUnit = 0.05, controller, onZoomChange, onMouseDown, onMouseUp, onWheel, children, ...props }: ZoomPannerProps): React.JSX.Element
{
	const ref = useRef<HTMLDivElement>(null);

	const [
		scaleState,
		setScaleState
	] = useState(defaultZoom);

	const [
		initOffsetState,
		setInitOffsetState
	] = useState<OffsetProps>();

	const [
		offsetState,
		setOffsetState
	] = useState<OffsetProps>({
		x: 0,
		y: 0
	});

	const zoom = useCallback<ZoomPannerControllerProps['zoom']>((value) =>
	{
		setScaleState(value);
	}, []);

	const zoomIn = useCallback(() =>
	{
		setScaleState((state) => state + zoomUnit);
	}, [ zoomUnit ]);

	const zoomOut = useCallback(() =>
	{
		setScaleState((state) =>
		{
			const value = (state - zoomUnit) * 10 / 10;

			//    
			if (value <= zoomUnit)
			{
				return state;
			}

			return value;
		});
	}, [ zoomUnit ]);

	const reset = useCallback(() =>
	{
		setScaleState(defaultZoom);
		setOffsetState({
			x: 0,
			y: 0
		});
		setInitOffsetState(undefined);
	}, [ defaultZoom ]);

	const handleMouseDown = useCallback<MouseEventHandler<HTMLDivElement>>((e) =>
	{
		onMouseDown?.(e);

		//   
		if (e.button === 0)
		{
			setInitOffsetState({
				x: e.clientX - (offsetState.x || 0),
				y: e.clientY - (offsetState.y || 0)
			});
		}
	}, [
		onMouseDown,
		offsetState
	]);

	const handleMouseUp = useCallback<MouseEventHandler<HTMLDivElement>>((e) =>
	{
		onMouseUp?.(e);

		setInitOffsetState(undefined);
	}, [ onMouseUp ]);

	const handleWheel: WheelEventHandler<HTMLDivElement> = useCallback((e) =>
	{
		onWheel?.(e);

		//  
		if (e.deltaY > 0)
		{
			zoomIn();
		}

		//  
		else
		{
			zoomOut();
		}
	}, [
		onWheel,
		zoomIn,
		zoomOut
	]);

	useEffect(() =>
	{
		const tag = ref.current;

		const handleMousemove = (e: MouseEvent): void =>
		{
			//  ,   
			if (initOffsetState && e.clientX > 0 && e.clientY > 0)
			{
				setOffsetState({
					x: (e.clientX - initOffsetState.x),
					y: (e.clientY - initOffsetState.y)
				});
			}
		};

		// DOM  
		if (tag)
		{
			window.addEventListener('mousemove', handleMousemove);
		}

		return (): void =>
		{
			// DOM  
			if (tag)
			{
				window.removeEventListener('mousemove', handleMousemove);
			}
		};
	}, [
		initOffsetState,
		zoomIn,
		zoomOut
	]);

	useEffect(() =>
	{
		onZoomChange?.(scaleState);
	}, [
		scaleState,
		onZoomChange
	]);

	useLayoutEffect(() =>
	{
		controller?.({
			reset,
			zoom,
			zoomIn,
			zoomOut
		});
	}, [
		controller,
		zoom,
		zoomIn,
		zoomOut,
		reset
	]);

	return (
		<div
			className="flex size-full cursor-move items-center justify-center overflow-hidden"
			data-component="ZoomPanner"
			ref={ref}
			onMouseDown={handleMouseDown}
			onMouseUp={handleMouseUp}
			onWheel={handleWheel}
			{...props}
		>
			<div
				className="pointer-events-none size-full select-none"
				draggable={false}
				style={{
					transform: `translate(${offsetState.x}px, ${offsetState.y}px) scale(${scaleState})`,
					transformOrigin: 'center'
				}}
			>
				{children}
			</div>
		</div>
	);
}